@charset "UTF-8";
/* =========================== */
.hidden {
  display: none;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

body,
ul,
li,
p,
h1,
h2,
h3,
a {
  margin: 0;
  padding: 0;
}

html {
  font-size: 100%;
  font-family: Arial, sans-serif;
}

img {
  max-width: 100%;
}

.page {
  background-color: #e3e1e8;
}

.flexible {
  max-width: 980px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.betony {
  text-transform: uppercase;
  color: #ae4b5d;
}

.breadcrumbs {
  background-color: #f3f3f3;
  padding: 0 24px;
}

.breadcrumbs__nav {
  width: 17%;
